@use "base/variables" as v;
@use "base/mixins" as m;

.banner {
  height: 30rem;
  @include m.tablet {
    height: 55rem;
  }
}
.banner-imagen {
  opacity: 0.7;
  padding: 0;
  margin: 0;
  height: 30rem;
  @include m.tablet {
    height: 55rem;
  }
  position: absolute;
}
.banner-texto {
  position: relative;
  top: 15rem;
  text-transform: uppercase;

  h2 {
    color: v.$negro;
    background-color:rgba(v.$verde,.8) ;
    padding: 0.7rem;
    font-size: 2rem;
  }
  margin: 0 auto;
  @include m.tablet {
    text-align: center;
    top: 25rem;
    width: fit-content;
    h2 {
      font-size: 4rem;
    }
  }
}
