@use 'base/mixins' as m;
@use 'base/variables' as v;

.todo{
    a{
        color: v.$negro;
        &:hover{
            color: v.$verde;
        }
    }
    img{
        position:absolute;
        height: 40rem;
        opacity: .4;
        @include m.tablet {
            height: 60rem;
            width: 100%;
        }
    }
}
.todos{
    height: 40rem;
    background-color: v.$negro;
    @include m.tablet {
    height: 60rem;
        
    }
}
.todo-titulo{
position: relative;
top:20rem;
background-color: v.$verde;
text-transform: uppercase;
padding:1rem;
margin: 0 auto;
width: fit-content;
font-size: 1.5rem;

cursor: pointer;
&:hover{
    background-color: v.$negro;
}
@include m.tablet {
    font-size: 3rem;
    
}
}