@use 'base/variables' as v;
@use 'base/mixins' as m;

.items{
    margin-bottom: 2rem;
    @include m.tablet {
        margin-top: 2rem;
        
    }
    @include m.tablet {
        @include m.grid(2,3rem);
        
    }
}
.destacados-titulo{
    color: v.$negro;
}
.destacados{
    background-image: url("/public/build/img/Documento\ A4\ Cuaderno\ Azul\ Liso\ \(1\).webp");
    background-size: 35rem;
    @include m.tablet {
        height: 100%;
    }
    height: 20rem;
    width: 100%;
    @include m.tablet {
        background-size: 30rem;
        
    }
    @include m.desktop {
        background-size: 40rem;
    }
    background-repeat: repeat-y;
    background-position: center top;
    margin-bottom: 3rem;
}
.productos{
    @include m.grid(2,3rem);
}
.producto{
    padding: 1rem;
    border:1px solid grey;
    display: flex;
    flex-direction: column;
    align-items: center;
    .producto-titulo{
        color: v.$verde;
        font-weight: bold;
        font-size: 1.5rem;
        @include m.tablet {
            font-size: 3rem;
            
        }
    }
    .producto-precio{
        color: v.$negro;
        font-weight: bold;
        margin-top: 1rem;
        font-size: 2rem;
        margin: 1rem;
    }
    .producto-agregar{
        text-transform: uppercase;
        padding: 1rem;
        cursor: pointer;
        color: v.$negro;
        font-weight: bold;
        border: solid 1px v.$negro;
        font-size: 2rem;
        &:hover{
        border: solid 1px v.$negro;
        background-color: v.$verde;

        }
    }
}